import ReactMarkdown from "react-markdown"

import { cn } from "@/lib/utils"

type MarkdownRendererProps = {
  children: string
  className?: string
}

export function MarkdownRenderer({
  children,
  className,
}: MarkdownRendererProps) {
  return (
    <ReactMarkdown
      className={cn("prose max-w-none text-foreground", className)}
    >
      {children}
    </ReactMarkdown>
  )
}
